
* {
  padding: 0;
  margin: 0;
}
i,
b {
  font-style: normal;
  display: inline-block;
  font-weight: normal;
}
html,
body {
  height: 100%;
  min-height: 100%;
  width: 100%;
  margin: 0 auto;
  padding: 0;
  background: #f3f3f3;
  -webkit-text-size-adjust: 100% !important;
}
a {
  text-decoration: none;
  outline: none !important;
}
ul li {
  list-style: none !important;
}
input,
textarea,
select {
  -webkit-tap-highlight-color: rgba(255, 0, 0, 0);
  outline: none;
}
.clearfix::before,
.clearfix::after {
  content: " ";
  display: table;
}
.clearfix::after {
  clear: both;
}
h1,
