  display: block;
}
#timeline .month-time {
  border-left: 1px solid #575757;
  box-sizing: border-box;
}
#timeline .month-time .year {
  width: 2.5rem;
  height: 26px;
  line-height: 26px;
  color: #333;
  border: 2px solid #fff;
  border-radius: 0 5px 5px 0;
  margin-left: -0.2rem;
  text-align: center !important;
  display: block;
}
#timeline .month-time .year_active {
  background: #666666;
  color: #fff;
}
#timeline .month-time ul {
  display: none;
}
#timeline .month-time ul > li {
  position: relative;
}
#timeline .month-time ul > li .work_msg {
  position: relative;
  font-size: 15px;
  color: #333;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
#timeline .month-time ul > li .work_msg span {
  padding: 14px 0;
  display: block;
}
#timeline .month-time ul > li .work_msg span > i {
  color: #a7a7a7;
  margin-left: -0.4375rem;
  font-size: 15px;
  font-weight: normal;
}
#timeline .month-time ul > li .work_msg .span_mouse {
  width: 2.5rem;
  float: left;
  text-align: center !important;
  margin-left: -0.2rem;
}
#timeline .month-time ul > li .work_msg .span_mouse .iconfont_dian {
  width: 8px;
  height: 8px;
  background: #666;
  overflow: hidden;
  display: inline-block;
  margin-top: -4px;
  padding-right: 4px;
  box-sizing: border-box;
  vertical-align: middle;
  border-radius: 50%;
  border: 2px solid #fff;
}
#timeline .month-time ul > li .work_msg .msg_div {
  width: 15.99rem;
}
#timeline .month-time ul > li .work_msg .msg_div a {
  color: #333;
}
#timeline .month-time ul > li .work_msg .msg_div div {
  position: relative;
  width: 15rem;
  overflow-x: auto;
}
#timeline .month-time ul > li .work_msg .msg_div div > ol {
  width: 11.71875;
  height: 3.90625rem;
  overflow: hidden;
}
